import Layout from '../components/Layout'
import { withRouter } from 'next/router'

const Content = (props) => (
  <div>
    {props.router.query.title}
    <p>This is the post content</p>
  </div>
)

const Post = withRouter(
  (props) => (
    <Layout>
      <Content />
    </Layout>
  )
)


export default Post